<template>
  <div>
    <b-spinner
      v-if="loading"
      style="width: 100px; height: 100px; position: fixed; top: 40%; left: 50%;"
      variant="secondary"
    ></b-spinner>
    <div style="text-align: center;" v-if="!readyToUse && !loading">
      <h2>Sorry, it's not ready to use yet.</h2>
      <h2>Go ahead and use the restroom, maybe play some ping pong.</h2>
      <h2>And then come back and refresh the page.</h2>
    </div>
    <b-container v-if="!loading && readyToUse">
      <b-row class="justify-content-md-center pt-2">
        <b-col cols="5">
          <b-card-group deck>
            <b-card id bg-variant="light" class="shadow p-3 mb-5 rounded">
              <p>
                Docker Image:<font-awesome-icon @click="refreshVersions" style="float: right; cursor: pointer;" icon="sync"></font-awesome-icon>
                <strong v-if="imageSelected.length < 1">{{ search }}</strong>
                <strong v-else>{{ imageSelected }}</strong>
              </p>
              <b-form-input
                size="sm"
                class="mr-sm-2"
                placeholder="Search for Image - Click to Select"
                type="text"
                v-model="search"
              ></b-form-input>
              <br/>
              <div style="max-height: 500px; overflow-y: scroll;">
                <b-list-group>
                  <b-list-group-item v-for="(item, index) in filteredItems" :key="index" button :active="item.selected" @click="()=>{onSelectVersion(item)}">{{item.dockerImage}}</b-list-group-item>
                </b-list-group>
              </div>
            </b-card>
          </b-card-group>
        </b-col>
        <b-col cols="5">
          <b-card-group deck>
            <b-card id bg-variant="light" class="shadow p-3 mb-5 rounded">
              <p>
                Sandbox CompanyID:
                <strong>{{ companySelected }}</strong>
              </p>
              <b-form-input
                size="sm"
                class="mr-sm-2"
                placeholder="Please type CompanyID here - example: 101693"
                v-model="companySelected"
                type="number"
                @keydown.enter="getCompanyInfoIfValid"
              ></b-form-input>
              <div class="pt-4 text-right">
                <b-button @click="getCompanyInfo" variant="success" class="shadow-sm p-2 rounded" v-bind:disabled="isDisabled || imageSelected === ''">
                  <b-spinner small v-if="loadingCompanyInfo"></b-spinner>
                  <strong>Run Update</strong>
                </b-button>
              </div>
              <b-modal size="lg" ref="are-you-sure-modal" centered hide-footer title="Does this look right? Double check!">
                <b-table striped thead-class="hidden_header" :items="modelTableItems"></b-table>
                <div>
                  <b-button style="float: left;" variant="outline-danger"  @click="hideModal">Cancel</b-button>
                  <b-button style="float: right;" variant="success" @click="runUpdate">Continue</b-button>
                </div>
              </b-modal>
              <b-modal ref="display-logs-modal" centered :no-close-on-backdrop="true" :no-close-on-esc="true" title="DB Updater Logs">
                <div id="oldschoolmodal-outer">
                  <i v-if="!doingAnUpdate" @click="closeDBUpdateModal" id="oldschoolmodal-close-text">X</i>
                  <div id="oldschoolmodal-screen">
                    <div ref="oldschoolmodal-output-area" id="oldschoolmodal-output" v-chat-scroll="{always: true, smooth: true}">
                      <p v-for="(item, index) in dbLogStatements" :key="index" :class="{ redMessage : item.isError }"> {{ item.text }}</p>
                    </div>
                  </div>
                  <div id="oldschoolmodal-led"></div>
                </div>
              </b-modal>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </b-container>
    <b-modal v-model="errored" centered hide-footer title="No Bueno..." header-bg-variant="danger" header-text-variant="dark">
      <p>{{errorMessage}}</p>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";
import { serverUrl } from "../config.js";
export default {
  name: "Updater",
  data() {
    return {
      search: '',
      imageSelected: '',
      branchSelected: '',
      versions: [],
      loading: true,
      readyToUse: false,
      loadingCompanyInfo: false,
      companyInfo: {},
      companySelected: '',
      errored: false,
      errorMessage: '',
      dbLogStatements: [],
      selectedImageId: 0,
      doingAnUpdate: false,
    };
  },
  computed: {
    filteredItems() {
      return this.versions.filter(item => {
        return item.dockerImage.toLowerCase().indexOf(this.search.trim().toLowerCase()) > -1;
      }).sort((a, b) => (a.dockerImage > b.dockerImage) ? 1 : -1);
    },
    isDisabled() {
      return this.companySelected.length !== 6;
    },
    modelTableItems() {
      return [
        {key: 'Image',      value: this.imageSelected},
        {key: 'Branch',     value: this.branchSelected},
        {key: 'CompanyID',  value: this.companySelected},
        {key: 'Sandbox',    value: this.companyInfo.sandboxName},
        {key: 'Database',   value: this.companyInfo.databaseName},
      ]
    }
  },
  methods: {
    showModal() {
      this.$refs["are-you-sure-modal"].show();
    },
    hideModal() {
      this.$refs["are-you-sure-modal"].hide();
    },
    handleError(errorMsg, errorObject) {
      const message = errorMsg ? errorMsg : errorObject;
      console.log(message);
      this.errorMessage = message;
      this.errored = true;
      if(typeof errorObject === 'object' && errorObject.response.status === 403) {
        window.location.href = "login";
      }
    },
    onSelectVersion(item) {
      this.imageSelected = item.dockerImage;
      this.branchSelected = item.imageBranch;
      this.selectedImageId = item.id;
      this.deselectAllVersions();

      item.selected = true;
    },
    deselectAllVersions() {
      for(let i = 0; i < this.versions.length; i++) {
        this.versions[i].selected = false;
      }
    },
    getCompanyInfoIfValid() {
      if(!this.isDisabled && this.imageSelected !== '') {
        this.getCompanyInfo();
      }
    },
    getCompanyInfo() {
      this.loadingCompanyInfo = true;
      axios
        .get(serverUrl + "/api/company/" + this.companySelected)
        .then(response => {
          this.companyInfo = response.data;
          if(!this.companyInfo.internal) {
            this.handleError("Whoa whoa whoa, that's a production database!!! You best make like a tree and get out of here.");
          }
          else {
            this.showModal();
          }
        })
        .catch(error => {
          this.loadingCompanyInfo = false;
          this.handleError("Failed to get company information for " + this.companySelected + ". Are you sure you put in the right company ID? Here's the actual error: " + error, error);
        })
        .finally(() => {
          this.loadingCompanyInfo = false;
        });
    },
    refreshVersions() {
      this.loading = true;
      this.imageSelected = '';
      this.deselectAllVersions();
      axios
        .get(serverUrl + "/api/versions")
        .then(response => {
          this.versions = response.data;
        })
        .catch(error => {
          this.loading = false;
          this.handleError(null, error);
        })
        .finally(() => (this.loading = false));
    },
    addLogMessage(msg, error) {
      this.dbLogStatements.push({
        text: msg,
        isError: error
      });
    },
    runUpdate() {
      this.hideModal();
      this.doingAnUpdate = true;
      this.$refs["display-logs-modal"].show();
      const that = this;
      const evtSource = new EventSource(serverUrl + "/api/update/stream/" + that.selectedImageId + "/" + that.companySelected);
      evtSource.addEventListener('message', function(e) {
        if(e && e.data) {
          const msg = e.data;
          that.addLogMessage(msg, msg.includes('Error occurred'));
          if(msg.includes('DB update is complete')) {
            that.finishDBUpdate(evtSource);
          }
        }
      });
      evtSource.addEventListener('error', function(e) {
        that.addLogMessage(e.data, true);
        that.finishDBUpdate(evtSource);
      });
    },
    finishDBUpdate(evtSource) {
      evtSource.close();
      this.doingAnUpdate = false;
    },
    closeDBUpdateModal() {
      this.$refs["display-logs-modal"].hide();
      this.dbLogStatements = [];
    },
  },
  mounted() {
    axios
      .get(serverUrl + "/api/gitStatus")
      .then(response => {
        if(response.data === 'LOADING') {
          this.handleError("Before this service can be used it needs to clone a copy of the Evolution git repository, and it's still working on doing that. Please check back again later as it may take a long time.");
        }
        else if(response.data === 'FAILED') {
          this.handleError("Something went wrong while the service was trying to initialize the Evolution git repo... someone will need to look at the service logs to see what went wrong.");
        }
        else {
          this.readyToUse = true;
          return axios
            .get(serverUrl + "/api/versions")
            .then(response => {
              this.versions = response.data;
            })
            .catch(error => {
              this.loading = false;
              this.handleError(null, error);
            })
            .finally(() => (this.loading = false));
        }
      })
      .catch(error => {
        this.handleError(null, error);
      })
      .finally(() => (this.loading = false));
  }
};
</script>

