<template>
  <div class="main">
    <b-spinner
      v-if="loading"
      style="width: 100px; height: 100px; position: fixed; top: 40%; left: 50%;"
      variant="secondary"
    ></b-spinner>
    <div v-if="!loading">
      <b-navbar toggleable="lg" type="dark" variant="purple" class="shadow p-2 mb-4">
        <b-navbar-brand style="padding-left:20%;">Remote Internal DB Updater</b-navbar-brand>

        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item-dropdown right>
            <!-- Using 'button-content' slot -->
            <template slot="button-content"><em>Sup {{userName}}</em></template>
            <b-dropdown-item href="/logout">Sign Out</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-navbar>
    </div>
    <Updater/>
    <b-container>
      <div class="copyright">
        <p>©2019-2112 CDK Global, LLC - <a style="color: white;" target="_blank" href="https://www.youtube.com/watch?v=dQw4w9WgXcQ">Recreation</a></p>
      </div>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import { serverUrl } from "../config.js";
import Updater from "./Updater";

export default {
  name: "Dashboard",
  components: {
    Updater
  },
  data() {
    return {
      loading: true,
      userName: 'Buddy'
    };
  },
  mounted() {
    axios
      .get(serverUrl + "/api/username")
      .then(response => {
        this.userName = response.data;
      })
      .catch(error => {
        console.log("Failed to lookup username " + error);
        this.loading = false;
      })
      .finally(() => (this.loading = false));
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bg-purple {
  background-color: rgba(37, 36, 57, 0.9) !important;
  /* background-color: #343a40 !important; */
}
.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 25%;
  width: 100%;
  background-color: rgba(22, 177, 181, 0.8);
  color: white;
  text-align: center;
  box-shadow: 3px 3px 5px 6px #ccc;
}
.copyright {
  padding: 14px;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 50px;
  width: 100%;
  background-color: #207e89;
  color: #e0e0e0;
  text-align: center;
}
</style>



